import React from 'react'

interface SpinnerProps {
    color?: 'red' | 'green' | 'blue' | 'black' | 'yellow'
    className?: string
}

export const Spinner = ({ color, className, ...props }: SpinnerProps) => {
    const choiseColor = (color: SpinnerProps['color']) => {
        switch (color) {
            case 'red':
                return 'text-red-500'
            case 'green':
                return 'text-green-500'
            case 'blue':
                return 'text-blue-500'
            case 'black':
                return 'text-black-500'
            case 'yellow':
                return 'text-yellow-500'
            default:
                return ''
        }
    }

    return <div className={`spinner-border ${choiseColor(color)} ${className}`} {...props}></div>
}
